$selectedColor: rgb(200, 200, 0);

.gamecell {
  width: 5rem;
  height: 5rem;
  border: 0.05rem lightgray solid;
  position: relative;
  box-sizing: border-box;
  aspect-ratio: 1;
}

.selected {
  border: 4px $selectedColor solid;
  background-color: lighten($selectedColor, 40%) !important;
}

.blankCell {
  background-color: black;
}

.hintCell {
  background-color: black;
  color: white;
}

.numberCell {
  background-color: white;
}
